import express from 'express';
import { celebrate, Joi } from 'celebrate';
import changePassword from '../controllers/changepassword_post';
import { baseUrl } from '../baseurl';

const router = express.Router();

const passwordChangeSchema = {
  body: Joi.object({
    oldPassword: Joi.string().required(),
    newPassword: Joi.string().required(),
  }),
};
/**
 * @swagger
 * /auth/changePassword:
 *   post:
 *     tags:
 *       - Authentication
 *     description: Returns status 200 if password is changed successfully
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: header
 *         name: Authorization
 *         schema:
 *           type: string
 *           required: true
 *       - in: "body"
 *         name: "body"
 *         description: "Required user credentials"
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *              oldPassword:
 *                  type: string
 *              newPassword:
 *                  type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *              message:
 *                  type: string
 *       400:
 *         description: Returns 400 if the password does not match with the existing one.
 *         schema:
 *           type: object
 *           properties:
 *              statusCode:
 *                  type: number
 *                  example: 400
 *              error:
 *                  type: string
 *              message:
 *                  type: string
 */

router.post(
  `${baseUrl}/changePassword`,
  celebrate(passwordChangeSchema),
  changePassword
);

export default router;
